import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Link from '@mui/material/Link';


export default function MenuResult(props) {

  const { meat, veg, result } = props;


  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell></TableCell>
            {
              Math.round(meat) ?
                [...Array(Math.round(meat))].map((e, index) => <TableCell key={index} align="left">{index + 1}</TableCell>)
                : null
            }
            {
              Math.round(veg) ?
                [...Array(Math.round(veg))].map((e, index) => <TableCell key={index} align="left">{index + 1}</TableCell>)
                : null
            }
          </TableRow>
        </TableHead>
        <TableBody>
          {
            (meat == 0 && veg == 0) ? null :

              result.map((e, index) =>
                e.date ?
                  <TableRow
                    key={index}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  >
                    <TableCell component="th" scope="row">
                      {e.date}
                    </TableCell>
                    {meat == 0 ? null :
                      e.meatDish.map((n, index) =>
                        <TableCell key={index} component="th" scope="row">
                          <Link color="inherit" target="_blank" rel="noopener" underline="hover" href={n.link}>{n.name}</Link>
                        </TableCell>
                      )
                    }
                    {veg == 0 ? null :
                      e.vegDish.map((n, index) =>
                        <TableCell key={index} component="th" scope="row">
                          <Link color="inherit" target="_blank" rel="noopener" underline="hover" href={n.link}>{n.name}</Link>
                        </TableCell>
                      )
                    }


                  </TableRow>
                  : null
              )
          }

        </TableBody>
      </Table>
    </TableContainer>
  );
}
