import * as React from 'react';
import Box from '@mui/material/Box';
import DatePicker from "./datePicker";
import TextField from '@mui/material/TextField';


import Stack from '@mui/material/Stack';


export default function OptionsPicker(props) {

    const { date, meat, veg, repeat, setMeat, setVeg, setDate, setRepeat } = props


    const changeMeat = e => {
        setMeat(e.target.value)
    }
    const ChangeVeg = e => {
        setVeg(e.target.value)
    }
    const changeRepeat = e => {
        setRepeat(e.target.value)
    }



    return (
        <Box
            component="form"
            sx={{
                '& .MuiTextField-root': { m: 1, width: '25ch' },
            }}
            Validate
            autoComplete="off"
        >
            <Stack spacing={2}>
                <DatePicker date={date} setDate={setDate} />
                <TextField id="meat" label="" variant="standard"
                    inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}
                    helperText="0" value={meat}
                    onChange={changeMeat}
                />
                <TextField id="veg" label="" variant="standard"
                    inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}
                    helperText="0" value={veg}
                    onChange={ChangeVeg}
                />
                <TextField id="noRepeateDays" label="" variant="standard"
                    inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}
                    helperText="" value={repeat}
                    onChange={changeRepeat}
                />
            </Stack>
        </Box>
    );
}