import React, { useState } from 'react';
import OptionsPicker from './optionsPicker';
import MenuResult from './menuResult';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';

import dishes from '../dishes.json';
import zIndex from '@mui/material/styles/zIndex';

export default function Generator() {

    const [date, setDate] = useState(['', '', '', '', '', '', '']);
    const [meat, setMeat] = useState(2);
    const [veg, setVeg] = useState(1);
    const [repeat, setRepeat] = useState(3);
    const [result, setResult] = useState([]);

    let meatDish = dishes.filter(e => e.meat === true);
    let vegDish = dishes.filter(e => e.meat !== true);



    function shuffleAndPick(arr, num) {
        let array = arr;
        let m = array.length, t, i;
        while (m) {
            i = Math.floor(Math.random() * m--);
            t = array[m];
            array[m] = array[i];
            array[i] = t;
        }

        return array.slice(0, num);
    }

    function subGroup(array, subGroupLength) {
        let index = 0;
        let newArray = [];
        while (index < array.length) {
            newArray.push(array.slice(index, index += parseInt(subGroupLength)));
        }
        return newArray;
    }

    function generate() {
        let days = date.filter(e => e !== '').length,
            meatNumber = meat * days,
            vegNumber = veg * days;

        if (meatDish.length < meatNumber) {
            alert('')
        } else if (vegDish.length < vegNumber) {
            alert('')
        }else if(!meatNumber && !vegNumber){
            alert('')
        }else {
            let pickedMeat = subGroup(shuffleAndPick(meatDish, meatNumber), meat);
            let pickedVeg = subGroup(shuffleAndPick(vegDish, vegNumber), veg);
            let preReuslt = [];

            date.map((value) => {
                preReuslt.push({
                    'date': value,
                    'meatDish': [],
                    'vegDish': []
                })
            })
            preReuslt.forEach(e => {
                if (e.date) {
                    e.meatDish = pickedMeat.splice(0,1)[0] || [];
                    e.vegDish = pickedVeg.splice(0,1)[0] || [];
                }
            })
            setResult(preReuslt);
            console.log(meatNumber,vegNumber)
            
        }
    }

    function fillUp() {

    }

    return (

        <Box sx={{ flexGrow: 1 }}>
            <Grid container spacing={2}>
                <Grid item xs={4}>
                    <OptionsPicker
                        date={date}
                        meat={meat}
                        veg={veg}
                        repeat={repeat}
                        setDate={setDate}
                        setMeat={setMeat}
                        setVeg={setVeg}
                        setRepeat={setRepeat}
                    />

                    <Stack direction="row" spacing={2}>
                        <Button onClick={generate} variant="contained"></Button>
                        <Button onClick={fillUp} variant="contained"></Button>
                    </Stack>

                </Grid>
                <Grid item xs={8}>
                    <MenuResult meat={meat} veg={veg} result={result} />
                </Grid>
            </Grid>
        </Box>
    );
}